
// Stripes
// ––––––––––––––––––––––––––––––––––––––––––––––––––

.stripes {
  display: grid;
  grid: repeat(5, 200px) / repeat(10, 1fr);
   :nth-child(1) {
    grid-column: span 3; }
   :nth-child(2) {
    grid-area: 3 / span 3 / auto / -1; }
   :nth-child(3) {
    grid-row: 4;
    grid-column: span 5; }
  transform: skewY(-12deg);
  transform-origin: 0;
  background: linear-gradient(#507EB1, #709DC7 10%, #DDE9F5 40%, #EAF2F9 50%, #F6F9FC 60%);
  grid-template-columns: repeat(5, 1fr);
   :nth-child(2) {
    background-color: red; } }
